:root{font-size:16px}*,*::before,*::after{box-sizing:border-box}body{margin:0;font-family:"Open Sans",sans-serif;width:100vw;height:auto;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:rgba(255,255,255,0.746)}h1,h2,h3,h4{font-family:"Montserrat",sans-serif}a{text-decoration:none}ul{padding:0}ul li{list-style:none}

.navbar{display:flex;justify-content:space-between;align-items:center;position:fixed;top:0;width:100%;height:5rem;padding:0 2rem;background-color:#000;color:#fff;opacity:.8;z-index:99999}.navbar__logo{color:aqua;font-size:2.5rem;font-weight:900;transition:.2s;-webkit-transform:scale(0.7);transform:scale(0.7)}.navbar__logo:hover{color:#0f9b9b}.navbar__menu{display:flex;flex-basis:40%;justify-content:flex-end;margin-bottom:0}.navbar__link{margin-right:3rem;font-size:1rem;color:#fff;transition:.4s}.navbar__link:hover{color:#adadad}

.switch-toggle{display:flex;height:20%;align-items:center}.switch-btn,.layer{position:absolute;top:0;right:0;bottom:0;left:0}.button-check{position:relative;width:90px;height:46px;overflow:hidden;border-radius:50px;-webkit-border-radius:50px;-moz-border-radius:50px;-ms-border-radius:50px;-o-border-radius:50px}.checkbox{position:relative;width:100%;height:100%;padding:0;margin:0;opacity:0;cursor:pointer;z-index:3}.switch-btn{z-index:2}.layer{width:100%;background-color:#8cf7a0;transition:0.3s ease all;z-index:1}#button-check .switch-btn:before,#button-check .switch-btn:after{position:absolute;top:4px;left:4px;width:30px;height:20px;color:#fff;font-size:10px;font-weight:bold;text-align:center;line-height:1;padding:9px 4px;background-color:#00921c;border-radius:50%;transition:0.3s cubic-bezier(0.18, 0.89, 0.35, 1.15) all;display:flex;align-items:center;justify-content:center}#button-check .switch-btn:before{content:'ON'}#button-check .switch-btn:after{content:'OFF'}#button-check .switch-btn:after{right:-50px;left:auto;background-color:#F44336}#button-check .checkbox:checked+.switch-btn:before{left:-50px}#button-check .checkbox:checked+.switch-btn:after{right:4px}#button-check .checkbox:checked ~ .layer{background-color:#fdd1d1}

.card__grid{display:grid;grid-template-columns:repeat(3, 250px);grid-gap:1.5rem;gap:1.5rem;margin:5rem auto;max-width:1024px;justify-content:center;align-items:center}.card__item{position:relative;width:100%;max-height:300px;height:auto;transition:all .6s ease-in-out;overflow:hidden}.card__item__img{display:block;width:100%;height:200px;object-fit:cover}.card__item__content{display:flex;justify-content:space-between;align-items:center;width:100%;padding:1rem;background-color:#fff}.card__item__content__left{line-height:1rem}.card__item__content__left small{font-size:.7rem}.card__item__content__left h6{font-size:1rem;font-weight:900}.card__item:hover{opacity:.8;-webkit-transform:scale(1.01);transform:scale(1.01)}.badge--purple{font-size:.5rem;background-color:purple;padding:.8rem .8rem}

.swiper-container{width:100%;height:250px}.swiper-slide{text-align:center;font-size:18px;opacity:.4;transition:opacity .5s ease-in-out}.swiper-slide-active{opacity:1}.swiper-slide img{display:block;width:100%;height:100%;object-fit:cover;z-index:-1}

.homepage{margin-top:7rem}.homepage__header{font-family:"Open Sans",sans-serif;font-weight:900;font-size:2.25rem}

.topgamesCardWrapper{display:grid;grid-template-columns:1fr;grid-gap:1.5rem;gap:1.5rem;width:100%;margin:5rem 0}.topgamesCard{display:flex;flex-direction:column;padding:1rem;color:black;transition:-webkit-transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);transition:transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);transition:transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1), -webkit-transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1)}.topgamesCard:active,.topgamesCard:hover{color:#777;-webkit-transform:scale(1.05);transform:scale(1.05)}.topgamesCard .topgamesCard__img{overflow:hidden}.ranking{font-size:3.5rem}.description{font-size:1.2rem;width:100%;margin:2rem 0;word-wrap:break-word}.release-date,.title{font-weight:900}@media screen and (min-width: 600px){.topgamesCard{flex-direction:row}.topgamesCard__img{flex-grow:1;display:flex;justify-content:space-between;align-items:center}.topgamesCard__img h2{flex:1 1;margin-right:1.5rem}.topgamesCard__content{flex-basis:60%;margin-left:2rem}}

.gamefilter-wrapper{display:flex;flex-direction:column;justify-content:space-between}.gamefilter-wrapper>*{display:flex;justify-content:space-between;align-items:center}.gamefilter-wrapper>*>:nth-child(1){margin-right:1rem}@media screen and (min-width: 600px){.gamefilter-wrapper{flex-direction:row}.gamefilter-wrapper>*>:nth-child(1){margin-bottom:0}}

.topgames{margin-top:7rem}.topgames__header{font-family:"Open Sans",sans-serif;font-weight:900;font-size:2.25rem}

